}

.progr-bar {
  width: 500px;
  position: absolute;
  bottom: 10%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border-radius: 4px;
}

.currentTime {
  width: 10%;
  float: left;
  height: 12px;
  font-size: 15px;
  color: #e5e5e5;
  padding-right: 10px;
  position: relative;
  bottom: 6px;
}

.duration {
  padding-left: 15px;
  padding-bottom: 10px;
  float: left;
  width: 10%;
  height: 12px;
  font-size: 15px;
  color: #e5e5e5;
  position: relative;
  bottom: 6px;
}

.slider {
  -webkit-appearance: none;
  width: 120px;
  height: 5px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
  float: right;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: rgb(76, 141, 50);
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: rgb(76, 141, 50);
  cursor: pointer;
}

.volume-slider {
  width: 150px;
  position: absolute;
  bottom: 22%;
  right: -60px;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.volume-icon {
